.vz-product-thumb_jag {
  .add-to-fave {
    font-size: 18px;
    cursor: pointer;
    color: red;
    button {
      outline: none;
      border: none;
      background: none;
      height: auto;
      line-height: 1;
      &:not(.js__filed):hover {
        animation: PulseFadeOut .4s ease;
      }
    }
  }
}

@keyframes PulseFadeOut {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0
  }
  100% {
    transform: scale(1);
    opacity: 1
  }
}